@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
.myPrice {
    color: #FF9200 !important;
    font-size: 18px !important;
}

.myItemName {
    overflow: hidden;
    text-transform: capitalize;
    height: 44px;
}

.myImageCardList {
    width: 100%;
    height: 100%;
    object-fit: fill;
}

.myAddButton {
    z-index: 10 !important;
    right: -4px !important;
    background-color: rgba(32, 132, 208, 0.7) !important;
    position: absolute;
    bottom: 0;
}

.myAddButton:hover {
    background-color: rgba(32, 132, 208, 1) !important;
}

.myItemContainer {
    margin: 5px 30px !important;
}

.myImageContainer {
    width: 290px;
    height: 290px;
    position: relative;
}
.myItemInfo {
    font-size: 15px;
}

.myItemViewPrice {
    margin: 0;
    color: #FF9200;
    font-size: 35px;
    display: inline-block !important;
}

@media screen and (min-width:768px) {
    .myItemInfo {
        font-size: 20px;
    }
}
