.myNavBar {

  margin: auto !important;
  width: 100% !important;
  padding: 0px !important;

}

.myNavBarLink {
  padding: 2px;
  font-size: 15px !important;
  font-weight: bolder !important;
  font-family: 'Oswald', sans-serif !important;
}

.thanhLogo {
  border-radius: 10px;
  padding-right: 2px;

}

@media screen and (min-width:992px) {
  .myNavBar {
    width: 85vw !important;
  }

  .myNavBarLink {
    padding: 0;
    font-size: 20px !important;
  }
}

.thanhLogo {
  width: 60px !important;
  padding-right: 5px;
}

.badge {
  background-color: rgb(241, 20, 20);
  border-radius: 10px;
  color: white;
  display: inline-block;
  font-size: 12px;
  line-height: 1;
  padding: 3px 7px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}
body {
    font-family: 'Lora', serif;

}

.login-box {
    background-color: rgba(128, 128, 128, 0.459);
    border-radius: 10px;
    border: 10px;
    width: 100%;
    left: auto !important;
    overflow: hidden;
    top: 25%;
    margin-left: auto;
    color: black;
    text-transform: uppercase;
}


.login-box h1 {
    text-align: center;

    font-size: 30px;
    border-bottom: 6px solid;
    padding: 10px 0;
    border-bottom: 3px solid rgba(36, 58, 54, 0.788);
}

.login-box label {
    text-align: center;
}

.textbox {
    margin: auto !important;
    width: 80%;
    overflow: hidden;
    float: center;
    font-size: 20px;
    padding: 5px 0;
    margin: 5px 0;


}

.textbox input {
    border: none;
    outline: none;
    background: none;
    color: white;
    font-size: 15px;
    width: 80%;
    float: left;
    margin: 0 10px;
}

.my-login-button {
    margin-left: 50% !important;

    transform: translate(-50%, -5%);
    width: 100%;
    height: 45px;

}

.my-google-login {

    top: 400px !important;
    margin: 2px auto !important;
    width: 100%;
    height: 45px;
    display: flex !important;
    justify-content: center !important;

}

.login-container {
    top: 100px !important;
    position: relative !important;
    width: 100% !important;
}



@media(min-width:480px) {
    .my-login-button {
        width: 30%;
    }

    .my-google-login {
        width: 30%;
    }

    .login-box {
        width: 50%;
        justify-content: center !important;
        margin: auto !important;

    }
}

.my_forgot_password_text {
    text-transform: capitalize;
    color: gray;
    text-decoration: underline;
    cursor: pointer;
    display: inline-block
}
.myItemNameCart {
    color: #0167C0;
    font-size: 15px;
    margin: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    text-overflow: ellipsis;

}


.myRemoveButton {
    margin-top: 5px !important;
    margin-left: -5px !important;
    top: 70%;
    transform: translateY(-50%);
    position: absolute;
    top: 80%;
}

.myCartViewPrice {
    color: #FF9200;
    top: 50%;

    font-size: 20px;
    position: absolute;
    transform: translateY(-50%);

}

.totalAmountAndPaypalContainer {
    margin-top: 30px;
}

.myPaypalContainer {
    margin: auto;
    width: 50vw !important;

}

.myTotalAmount {
    color: #FF9200;
    font-size: 30px;
}

.itemContainerCart {
    border-bottom: solid 1px rgba(128, 128, 128, 0.5);
}

@media screen and (min-width:768px) {
    .myItemNameCart {
        font-size: 25px;

    }

    .myItemNameCart {
        font-size: 20px;
    }



    .myCartViewPrice {
        margin-left: 40px !important;
    }

    .myRemoveButton {
        margin-left: 40px !important;
        top: 85%;
    }
}
